<template>
    <div class="gray-bg">
      <div class="middle-box text-center loginscreen   animated fadeInDown">
        <div>
          <div>

            <h1 class="logo-name">H+</h1>

          </div>
          <h3>欢迎注册</h3>
          <form class="m-t" role="form" action="customerlogin.html">
            <div class="form-group">
              <input type="text" class="form-control" placeholder="请输入用户名" required="">
            </div>
            <div class="form-group">
              <input type="password" class="form-control" placeholder="请输入密码" required="">
            </div>
            <div class="form-group">
              <input type="password" class="form-control" placeholder="请再次输入密码" required="">
            </div>
            <div class="form-group text-left">
              <div class="checkbox i-checks">
                <label class="no-padding">
                  <input type="checkbox"><i></i> 我同意注册协议</label>
              </div>
            </div>
            <button type="submit" class="btn btn-primary block full-width m-b">注 册</button>

            <p class="text-muted text-center"><small>已经有账户了？</small><a href="customerlogin.html">点此登录</a>
            </p>

          </form>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "register"
    }
</script>

<style scoped>
  @import "../../static/css/bootstrap.min.css";
  @import "../../static/css/font-awesome.css";
  @import "../../static/css/animate.css";
  @import "../../static/css/style.css";
  @import "../../static/css/plugins/iCheck/custom.css";
</style>



